.landing {
  min-height: 100vh;
  max-width: 100vw;
  background: url("./../img/beach.webp") no-repeat center / cover;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 0;

  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.1);
    z-index: 2;
  }
  & * {
    position: relative;
    z-index: 10;
  }

  .heading {
    width: 80%;
    margin: 2.5rem auto 2rem;
    height: auto;

    li {
      padding: 1.5rem;

      a {
        padding: 5px;
        color: $primary;
      }
    }
  }

  .showcase {
    flex-grow: 1;
    margin-bottom: 9rem;
    flex-direction: column;
  }
}
